@import url(http://weloveiconfonts.com/api/?family=fontawesome);
.dropdown {
	position: relative;
	display: inline-block;
	cursor: pointer;
}

.drop {
	width: 55px;
	height: 18px;
	background-color: #add8e6;
	padding: 12px;
	border-radius: 5px;
	font-weight: bold;
	color: white;
}

.drop:before {
	content: "";
	position: absolute;
	width: 0px;
	height: 0px;
	border: 10px solid;
	border-color: white transparent transparent transparent;
	right: 6px;
	top:18px;
}

.ddMenu {
	margin: 0;
	padding: 0;
	top: 45px;
	right: 0px;
	width: 50px;
	background-color: white;
	font-weight: bold;
	position: absolute;
	box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
	z-index: 1;
}

button {
	width: 75px;
	padding: 8px 16px;
	border-bottom: 1px solid #e5e5e5;
}

